import { Table } from 'react-bootstrap'
import { useDispatch, useSelector } from 'react-redux'
import { plusCount, minusCount, deleteItem } from './../store.js';
import { changeName } from '../store/userSlice.js';
import { useState, memo, useMemo } from 'react';


function Cart() {

  // let state = useSelector((state) => {return state});
  let userCart = useSelector((state) => state.userCart);
  let user = useSelector((state) => state.user);
  let dispatch = useDispatch()

  return (
    <div>   
      <Table>
        <thead>
          <tr>
            <th>#</th>
            <th>상품명</th>
            <th>수량</th>
            <th>변경하기</th>
            <th>삭제하기</th>
          </tr>
        </thead>
        <tbody>
            {
              userCart.map(function (a, i) {
                return (
                  <tr key={i}>
                    <td>{userCart[i].id}</td>
                    <td>{userCart[i].title}</td>
                    <td>{userCart[i].count}</td>
                    <td><button onClick={() => {
                      dispatch(plusCount(userCart[i].id));
                    }}>+</button><button onClick={() => {
                      dispatch(minusCount(userCart[i].id));
                    }}>-</button></td>
                    <td style={{cursor:'pointer'}} onClick={() =>{
                      dispatch(deleteItem(userCart[i].id));
                    }}>🗑️</td>
                  </tr>
                )
              })
            }


        </tbody>
      </Table>
    </div>
  )
}

export default Cart